  .product{
    position: relative;
    width: 50%;
  }
    
  .product:hover .overlay {
    bottom: 0;
    height: 300px;
  }
  .subtext {
    color: rgb(17, 17, 17);
    font-size: 20px;
    position: relative;
    top: 50%;
    left: 60%;
    -webkit-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
  }
  .productoverlay {
    position: absolute;
    bottom: 100%;
    left: 70px;
    right: 70px;
    background-color: #ffffff;
    overflow: hidden;
    width: auto;
    height:0;
    transition: .5s ease;
    top: 75px;
  }
  .productcollections {
    color: rgb(17, 17, 17);
    font-size: 20px;
    position: relative;
    top: 50%;
    left: 55%;
    -webkit-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
  }

  /* classes from Dropdown.css */
  .menudropdown {
    float: left;
    overflow: hidden;
  }

  /* Dropdown content (hidden by default) */
.menudropdown-content {
  display: none;
  position: fixed;
  width: 96%;
  left: 2%;
  padding-top: 1rem;
}

/* Create three equal columns that floats next to each other */
.menucolumn {
  /* float: left; */
  /* width: 20%; */
    padding: 1rem;
    vertical-align: bottom;
    align-items: center;
    text-align: center;
}

.dropmenu-image {
  display: none;
}

